<!-- Echarts 柱状图 -->
<template>
  <div :id="domId"
       style="width: 100%;height:100%;"></div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
/**
let option = {
  legend: {
    data: []
  },
  series: [
    {
      data: [],
      name: '',
    },
    {
      data: [],
      name: '',
    }
  ]
}
 */
export default {
  props: ['domId', 'option', 'title'],
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    //这里存放数据
    return {
      myChart: null,
      myOption: {}
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {
    option (n, o) {
      this.myOption = n
      this.setOption()
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {

  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.init()

  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    init () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(document.getElementById(this.domId));
      let self = this
      window.onresize = function () {
        self.myChart.resize()
      }
      this.initOption()
    },
    initOption () {
      let option = {
        title: {
          text: this.title,
          left: '5%',
          top: '10',
          textStyle: {
            fontSize: 17,
          },
        },
        tooltip: {
          trigger: 'item',
        },
        legend: {
          top: 'center',
          right: 10,
          orient: 'vertical',
          align: 'left'
        },
        series: [
          {
            color: ['rgb(79, 105, 252)', 'rgb(255, 35, 35)'],
            type: 'pie',
            radius: [0, '30%'],
            center: ['40%', '55%'],
            label: {
              position: 'inner',
              fontSize: 14,
            },
            data: [
            ]
          },
          {
            type: 'pie',
            radius: ['45%', '60%'],
            center: ['40%', '55%'],
            label: {
              show: false,
              position: 'insideRight'
            },
            data: [
            ]
          }
        ]

      };
      this.myChart.setOption(option);

    },
    setOption () {
      this.myChart.setOption(this.myOption)
    }

  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
.a {
  color: rgb(255, 35, 35);
}
</style>