<template>
  <van-row>
    <!-- 顶部框 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">游客画像分析</p>
      </van-col>
    </van-row>
    <van-row class="dt-content">
      <div class="dt-charts">
        <ageSexBar domId="ageSexBar"
                   :option="option1"
                   title="昨日入园人数分析">
        </ageSexBar>
      </div>
      <!-- 常住地 各省 -->
      <div class="dt-charts">
        <cityBar domId="propertyBar"
                 :option="option2"
                 title="昨日各省入园人员常驻地（TOP5）">
        </cityBar>
      </div>
      <!-- 常住地 省内各市 -->
      <div class="dt-charts">
        <cityBar domId="cityBar"
                 :option="option3"
                 title="昨日省内入园人员常驻地">
        </cityBar>
      </div>
      <!-- 旅游距离分析 -->
      <div class="dt-charts dt-charts-last">
        <distancePie domId="distancePie"
                     :option="option4"
                     title="昨日旅游距离分析">
        </distancePie>
      </div>
    </van-row>
  </van-row>
</template>


<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
/** 引入组件 */
// import age from "@/components/echarts/ageDistribution";
// import sex from "@/components/echarts/sexDistribution";
import cityBar from "@/components/echarts/cityBar";
import ageSexBar from "@/components/echarts/ageSexBar";
import distancePie from "@/components/echarts/distancePie";
/**  引入接口  */
import axios from "axios";
import {
  sourcePlace,
  queryDistance,
  // gateNum,
  imageList,
} from "@/api/chart";

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    cityBar,
    ageSexBar,
    distancePie,
  },
  data () {
    //这里存放数据
    return {
      // 年龄/性别 比例分析
      option1: {},
      // 各省长驻地分析
      option2: {},
      // 省内长驻地分析
      option3: {},
      // 旅游距离分析
      option4: {},
      // 实时人数
      onNum: 0,
      // 昨日人数
      yesterday: 0,
      // 昨日各年龄段总人数人数
      ageTotle: 0,
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    // 获取男女比例及年龄分布
    this.getAgeSex()
    // 获取旅游距离
    this.getDistance()
    this.getSourcePlace()
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    Home () {
      this.$router.push({ name: 'Home' })
    },

    getAgeSex () {
      axios.all([
        imageList({ type: 'age' }),
        imageList({ type: 'gender' })
      ]).then(([age, gender]) => {
        let ageList = age.data
        let genderList = gender.data
        let legendData = []
        let ageData = []
        let genderData = []

        ageList.forEach(item => {
          legendData.push(item.property)
          ageData.push({
            value: item.people,
            name: item.property
          })
        })
        genderList.sort(a => {
          if (a.property == '女') {
            return 1
          } else if (a.property == '男') {
            return -1
          }
        })
        genderList.forEach(item => {
          legendData.push(item.property)
          genderData.push({
            value: item.people,
            name: item.property
          })
        })
        this.option1 = {
          legend: {
            data: legendData
          },
          series: [
            {
              data: genderData,
              name: '性别分布',
            },
            {
              data: ageData,
              name: '年龄分布',
            }
          ]
        }
      })
    },

    getDistance () {
      queryDistance().then(res => {
        console.log('res', res);
        let data = res.data
        let list = []
        data.forEach(item => {
          list.push({
            name: item.property,
            value: item.value
          })
        })
        this.option4 = {
          series: [
            {
              data: list
            }
          ]
        }
      })
    },

    getSourcePlace () {
      sourcePlace().then(res => {
        console.log("getSourcePlace", res);
        let provinceList = res.data.province
        let dataObj = {}
        provinceList.forEach(item => {
          dataObj[item.property] = item.people
        })
        let datakey = Object.keys(dataObj).sort(function (a, b) {
          if (dataObj[b] > dataObj[a]) {
            return -1
          } else {
            return 1
          }
        })
        datakey = datakey.slice(datakey.length - 5, datakey.length)
        let serData = []
        datakey.forEach(item => {
          serData.push(dataObj[item])
        })
        this.option2 = {
          color: ['#326FDC'],
          legend: {
            data: datakey
          },
          yAxis: {
            data: datakey
          },
          series: [
            {
              data: serData
            }
          ]
        }
        const CITYS = ['常州市', '徐州市', '南京市', '淮安市', '南通市', '宿迁市', '无锡市', '扬州市', '盐城市', '苏州市', '泰州市', '镇江市', '连云港市']
        let cityList = res.data.city
        let cityObj = {}
        cityList.forEach(item => {
          if (CITYS.indexOf(item.property) !== -1) {
            cityObj[item.property] = item.people
          }
        })
        let citykey = Object.keys(cityObj).sort(function (a, b) {
          if (cityObj[b] > cityObj[a]) {
            return -1
          } else {
            return 1
          }
        })
        let citySerData = []
        citykey.forEach(item => {
          citySerData.push(cityObj[item])
        })
        this.option3 = {
          color: ['#05C794'],
          legend: {
            data: citykey
          },
          yAxis: {
            data: citykey
          },
          dataZoom: [
            {
              id: 'dataZoomY',
              type: 'inside',
              yAxisIndex: [0],
              filterMode: 'filter',
              start: 60,
              end: 100
            }
          ],
          series: [
            {
              data: citySerData
            }
          ]
        }

      })
    },

    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../../styles/dimens';
.body {
  margin-top: 0px;
}
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: 'SourceHanSansCN-Medium';
  color: #ecedf2;
  // margin-top: px2rem(35);
}
.dt-content {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: calc(100vh - #{$headerHeight} - #{$statusBarHeight});
  background-color: #edf1f5;
  overflow-y: auto;
}
.dt-charts {
  width: 95%;
  height: px2rem(250);
  margin-top: px2rem(10);
  background: #ffffff;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  border-radius: 8px;
}
.dt-charts-last {
  margin-bottom: px2rem(10);
}
</style>
